@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css");

body {
  background-color: #F6F6F6;
  padding: 50px 25px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  max-width: 770px;
  width: 100%;
  margin: 0 auto;
}

.line {
  width: 750px;
}

.title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin: 0 10px 15px 0;
  font-weight: bold;
  letter-spacing: -3px;
}

h1 {
  font-size: 120px;
  text-align: left;
}

h2 {
  font-size: 60px;
  text-align: right;
}

.semititle {
  display: flex;
  margin: 20px 10px 80px 10px;
  justify-content: space-between;
}

.coloum {
  width: 150px;
  font-size: 9px;
  letter-spacing: 0.8px;
  line-height: 130%;
}

.coloum:nth-child(2) {
  flex-grow: 1;
}

.coloum:nth-child(3) {
  text-align: right;
}

h3 {
  text-transform: uppercase;
}

.circles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: end;
}

.circle {
  background: #BAE1F1;
  width: 230px;
  height: 230px;
  margin: 10px;
  border-radius: 50%;
}

.circle:nth-child(n+4) {
  background: #616365;
}

.circle:nth-child(n+7) {
  background: #03A464;
}